import { CqsItem } from "@rp/common/api-types"
import { isCompanySuggestion } from "./suggestion.type-guards"
import { EntitySuggestion } from "./suggestion.types"

export const convertSuggestionToCqsItem = (suggestion: EntitySuggestion) => {
  if (isCompanySuggestion(suggestion)) {
    const item: CqsItem = {
      entityType: suggestion.entityType,
      description: suggestion.description,
      volume: 0,
      group1: suggestion.group1,
      group2: suggestion.group2,
      group3: suggestion.group3,
      group4: suggestion.group4,
      group5: suggestion.group5,
      metadata1: suggestion.ticker ?? suggestion.metadata1,
      metadata2: suggestion.metadata2,
      metadata3: suggestion.metadata3,
      metadata4: suggestion.metadata4,
      category: suggestion.category,
      key: suggestion.key,
      name: suggestion.name,
      queryType: suggestion.queryType,
      queryRelationship: "result",
    }
    return item
  } else {
    const item: CqsItem = {
      entityType: suggestion.entityType,
      description: suggestion.description,
      volume: 0,
      group1: suggestion.group1,
      group2: suggestion.group2,
      group3: suggestion.group3,
      group4: suggestion.group4,
      group5: suggestion.group5,
      metadata1: suggestion.metadata1,
      category: suggestion.category,
      key: suggestion.key,
      name: suggestion.name,
      queryType: suggestion.queryType,
      queryRelationship: "result",
    }
    return item
  }
}
